.Nav {
  width: 60%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Nav .tab {
  font-size: 4rem;
  text-decoration: none;
  color: rgba(0, 0, 50, 0.7);
  padding: 0.2rem 3rem;
  margin: 0 0.5rem;
  border-bottom: 0.5rem solid transparent;
  white-space: nowrap;
}

.Nav .tab:hover {
  border-radius: 0 0 0.5rem 0.5rem;
  border-bottom: 0.5rem solid rgba(0, 0, 50, 0.4);
}

.Nav .tab.active {
  color: rgba(0, 0, 50, 0.8);
  border-radius: 0 0 0.5rem 0.5rem;
  border-bottom: 0.5rem solid rgba(0, 0, 50, 0.8);
  transition: all 1s ease-in-out;
}
